import { Menu } from "antd";
import { Link } from "react-router-dom";

export default function Home() {
  return (
    <>
      <Menu theme="dark" mode="horizontal">
        <Menu.Item key="home">
          <Link to="/">Home</Link>
        </Menu.Item>
        <Menu.Item key="seats">
          <Link to="/seats"></Link>
        </Menu.Item>
        <Menu.Item key="login">
          <Link to="/login"></Link>
        </Menu.Item>
        <Menu.Item key="dispatch">
          <Link to="/display"></Link>
        </Menu.Item>
        <Menu.Item key="verify">
          <Link to="/verify"></Link>
        </Menu.Item>
        <Menu.Item key="setting">
          <Link to="/setting"></Link>
        </Menu.Item>
      </Menu>
      <div> </div>
    </>
  );
}
